import React from "react";
import { useState, useEffect, useRef } from "react";
import typewriter from "react-typewriter-hook";
const MagicOcean = [
  "路遥遥其修选兮，吾将上下而求索！",
  "鲜衣怒马少年时，不负韶华行且知！",
  "少年自负凌云笔。而到今、春华落尽，满怀萧瑟！",
  "每天进步一点点，一点点，再来一点点！",
];
let index = 0;
export default function AutoTypewriter() {
  const [magicName, setMagicName] = useState(`书山有路勤为径，学海无涯苦作舟！`);
  const intervalRef = useRef({});
  const name = typewriter(magicName);
  useEffect(() => {
    intervalRef.current = setInterval(() => {
      index = index > MagicOcean.length - 1 ? 0 : ++index;
      console.log(index);
      setMagicName(MagicOcean[index]);
    }, 5000);
    return function clear() {
      console.log("clear");
      clearInterval(intervalRef.current);
    };
  }, [magicName]);
  return (
    <div
      style={{
        position: "absolute",
        left: "50%",
        top: "50%",
        transform: "translate(-50%,-50%)",
        fontSize: "25px",
      }}
    >
      「{name}」
    </div>
  );
}
